import clsx from 'clsx'
import React, { FC } from 'react'
import { Typography } from '../../other/Typography'
import './MenuRadioItem.scss'
import { MenuRadioItemProps } from './types'

const MenuRadioItem: FC<MenuRadioItemProps> = ({
  id,
  isDisabled = false,
  isChecked = false,
  title = '',
  subTitle = '',
  value,
  size = 'L',
  onClick,
}) => {
  return (
    <label
      className='MenuRadioItem'
      htmlFor={id}
      onClick={(): void => {
        if (!isDisabled) {
          onClick(value)
        }
      }}
    >
      <div
        className={clsx(
          'MenuRadioItem__RadioMarker',
          `--${size}`,
          isDisabled && '--disabled',
          isChecked && '--checked',
        )}
      >
        <input
          checked={isChecked}
          className='MenuRadioItem__Input'
          id={id}
          type='radio'
        />
      </div>
      <Typography
        className={clsx('MenuRadioItem__RadioMarker-Text', `--${size}`)}
        text={title}
        type='Caption1Bold'
      />
      <Typography
        className='MenuRadioItem__Price'
        text={subTitle}
        type='Body1B'
      />
    </label>
  )
}

export default MenuRadioItem
